<template>
  <section class="experience">
    <div class="section-title">
      <h3>专业技能</h3>
    </div>
    <ul class="experience-detail" v-for="item in info">
        <div class="experience-detail">
          <h4 v-text="item.text"></h4>
        </div>
    </ul>
  </section>
</template>

<script>
  export default {
    name: 'experience',
    data() {
      return {
        info: [
          {
            text:"熟悉C#,JAVA等面向对象语言,擅长使用IntelliJ IDEA、eclipse进行开发"
          },{
            text:"熟练运用到的框架有Spring+Spring MVC+Mybatis架构"
          },{
            text:"擅长SQLServer、MySQL等关系型数据库的视图、存储过程、事物、索引、以及触发器的运用 ;"
          },{
            text:"熟悉JQuery、Vue、BootStrap、ECharts、iView和Element UI等前端框架的基本使用"
          },{
            text:"熟悉AJAX,JavaScript等客户端脚本技术"
          },{
            text:"熟悉软件编码规范"
          }
        ],
      };
    },
    created() {
      getExperience().then((response) => {
        this.info = response.info;
      }).then(() => {
        if (document.getElementById('leftNav').clientHeight < document.getElementById('mainBoard').clientHeight) {
          document.getElementById('leftNav').style.height = `${document.getElementById('mainBoard').clientHeight}px`;
        }
      });
    },
  };
</script>

<style lang="scss" scoped>
  @import "../../style/common";

  .experience {
    padding-right: 40px;
  }


  .experience-title {
    width: 130px;

    h4 {
      color: #303133;
    }
  }

  .experience-detail {
    h4 {
      margin-bottom: 4px;
      padding: 15px;
      display: inline-block;
    }
  }


</style>
